<template>
  <div>
      <img v-if="data.name !== undefined" :src="require('../assets/items/' + data.name + '.svg')">
      <h1 class="label">{{data.label}}</h1>
      <div class="description_container">
        <h1 class="text">{{data.description}}</h1>
      </div>
      <div class="weight-container">
        <h1 class="weight right">{{ getData }}</h1>
        <h1 class="weight_label left">Peso</h1>
      </div>

      <div class="option_container">
          <h1 :class="{gray: !data.consumable, green: data.consumable}">Consumível</h1>
      </div>
  </div>
</template>

<script>
export default {
props: {
    data: Object
},
computed: {
    getData: function() {
        if(this.data.weight >= 1000) {
            return Math.floor(this.data.weight / 1000) + 'kg'
        } else {
            return this.data.weight + 'g'
        }
    }
},
}
</script>

