<template>
<div id="app">
  <transition name="fade">
    <div v-if="isInventoryOpen">
      <div>
        <inventory
        @show_info="showInfo"
        :user_items="item"
        :config="config"
      
        />
      </div>
      <div>
        <status
          :status="status"
        />
      </div>
      <showMoreInfo 
        class="moreInformation" 
        v-show="showMoreInfo.isShown"
        :data="temporaryInfoData"
      />
      <weaponSlots 
      
      />
    </div>
  </transition>
</div>
</template>

<script>
import inventory from './components/inventory.vue';
import status from './components/status.vue';
import showMoreInfo from './components/showMoreInfo.vue';
import weaponSlots from './components/weaponSlots.vue'
export default {
  components: {
    inventory,
    status,
    showMoreInfo,
    weaponSlots
  },
  data() {
    return {
      showMoreInfo: {
        timeout: null,
        isShown: false,
      },
      isInventoryOpen: false,
      status: {
        health: 0,
        water: 0,
        food: 0,
        stamina: 0,
      },

      item: {

      },

      config: {},
  
      temporaryInfoData: {}
    }
  },
  watch: {
    'item' : function (newVal, oldVal) {
      alt.emit('inventory:update', newVal)
    }
  },
  methods: {
    showInfo: function(state, e, object) {
      if(state) {
        if(this.showMoreInfo.timeout === null) {
          this.showMoreInfo.timeout = setTimeout(() => {
          
            this.showMoreInfo.isShown = true
          
            let element = document.querySelector('.moreInformation');
            element.style.top = e.clientY + 'px';
            element.style.left = e.clientX + 'px';
            this.temporaryInfoData = object;
          }, 500);
        }
      } else {
        if(this.showMoreInfo.timeout !== null) {
          clearTimeout(this.showMoreInfo.timeout);
          this.showMoreInfo.timeout = null;
          this.showMoreInfo.isShown = false;
        }
      }
    },

    loadInventory: function (items) {
      this.item = items;
    },

    openInventory: function (state) {
      this.isInventoryOpen = !this.isInventoryOpen;
      if(this.showMoreInfo.timeout) {
        clearTimeout(this.showMoreInfo.timeout)
        this.showMoreInfo.timeout = null;
      }

      this.showMoreInfo.isShown = false;
    },

    //set left side status
    setStatus: function (data) {
      this.status = data;
    },

    updateConfig: function(config) {
      this.config = config;
    },
  },
  mounted() {
    if('alt' in window) {
      setTimeout(() => {
        alt.emit('inventory:ready');
      }, 5);
      alt.on('inventory:loadInventory', this.loadInventory);
      alt.on('inventory:toggle', this.openInventory);


      alt.on('status:setStatus', this.setStatus);
      alt.on('inventory:config', this.updateConfig)
    }
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap');

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #305c88;
  user-select: none;
  margin-top: 60px;
  font-family: 'Open Sans', sans-serif;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .4s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.inventory {
  position: absolute;
  height: 70%;
  width: 20%;
  top: 10%;
  left: 30%;
}

.moreInformation {
  position: absolute;
  border-radius: 5px;
  z-index: 1000;
  width: 10%;
  padding: .5%;
  height: auto;
  background-color: rgba(22, 22, 22, 0.975);
}
</style>

