<template>
  <div id="inventory_container">
    <section class="inventoryHeader">
      <img src="../assets/ui_icon/bag.svg">
      <h3 class="label">INVENTÁRIO</h3>
    </section>

    <section id="inventory">
      <div 
        v-for="item of user_items" 
        :key="item.quantity"
        @mousedown="openOptions"
        id="inv_container_selector"
        :class="getRaretyColor(item.data)"
        @mouseover="toggleInfo(true, $event, item.data)"
        @mouseout="toggleInfo(false, $event, item.data)"
      >
        <div v-show="item.quantity > 1" class="quantity">
          <span v-show="item.data.name === 'money'">{{item.quantity}} €</span>
          <span v-show="item.data.name === 'cigarrete'">{{item.quantity}}</span>
        </div>
        <div class="img-container">
          <img :src="require('../assets/items/' + item.data.name + '.svg')">
        </div>
        <div class="container_label">
          <h1 class="label">{{item.data.label}}</h1>
        </div>
        <div class="damage_container" v-if="item.data.durability">
          <div class="fill"></div>
        </div>
      </div>

    </section>
    <div class="options" v-show="options.activated">

    </div>
    <section id="footer">
      <h1 class="label">Peso</h1>
      <img src="../assets/ui_icon/caixa.svg">
      <div class="bar">
        <div class="fill" id="fill"></div>
      </div>
      <h1 class="weight">{{currentWeight}}/{{config.max_weight}}kg</h1>
    </section>
  </div>
</template>

<script>
export default {
  props: {
    user_items: Array,
    config: Object
  },
  data() {
    return {
      currentWeight: 0,
      maxWeight: 50,
      options: {
        activated: false,
      },

      triggers: {
        showMoreInfo: false,
        hoverInformationTimeout: null
      },
    }
  },
  mounted() {
    if('alt' in window) {
      alt.on('inventory:weight', this.setWeight);
      alt.on('inventory:durability', this.setDurability);
    }
  },
  methods: {
    getRaretyColor: function (item) {
        return {
            "item-container wealth": item.type === 'wealth',
            "item-container common": item.type === 'common',
        }
    },

    setWeight: function (weight) {
      //Calculates the weight of the items and also the durability

      this.currentWeight = weight / 1000;
      alt.emit('inventory:inventoryWeight', this.currentWeight)
      let bar = 0;
      bar = document.getElementById('fill')
      bar.style.width = (this.currentWeight / this.maxWeight) * 100 + '%';
    },

    setDurability: function (durability) {
      //durability
      for (let i = 0; i < this.user_items.length; i++) {
        if(this.user_items[i].durability) {
          setTimeout(() => {
            let durability = document.querySelector('.damage_container .fill');
            durability.style.width = this.user_items[i].durability + '%';
          }, 1);
        }
      }
    },

    toggleInfo: function (state, e, obj) {
      this.$emit('show_info', state, e, obj)
    },

    openOptions: function (e) {
      if(e.which === 3) {
        this.options.activated = true
        const element = document.querySelector('.options')
        const parent = document.getElementById('inventory_container')

        element.style.position = 'absolute';
        element.style.zIndex = 1000;
        parent.append(element);
        element.style.top = e.pageY - e.offsetWeight / 2 + 'px';
        element.style.left = e.pageX - e.offsetHeight / 2 + 'px';
        console.log(element)
      }
    }
  },

}
</script>

